.wishlist-products-container {
  padding: 1em;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
}

.wishlist-products-container .img-container {
  height: 200px;
  width: 100%; /* 컨테이너 너비에 맞게 조정 */
  object-fit: contain; /* 이미지 비율을 유지하면서 컨테이너에 맞춤 */
  margin: 0 auto; /* 상하 마진 제거 및 가로 방향으로 중앙 정렬 */
  display: block; /* 블록 레벨 요소로 설정하여 margin auto 적용 */
  overflow: hidden; /* 컨테이너를 벗어나는 이미지 부분은 숨김 */
}

.wishlist-card {
  padding: 1em 1em;
  box-shadow: 0px 0px 46px -4px rgb(238, 238, 238);
  margin: 1em 1em 2em 1em;
  border-radius: 1em;
  width: 280px;
  height: 480px;
  justify-content: space-between;
  display: flex;
  flex-flow: column nowrap;
}


.wishlist-products-container .img-container img {
  max-width: 100%; /* 최대 너비를 100%로 설정하여 오버플로 방지 */
  height: auto; /* 원본 비율 유지 */
  object-fit: contain; /* 컨테이너에 맞게 이미지 조정, 필요시 여백 발생 */
}

.wishlist-btn-container {
  position: relative;
  flex-grow: 1;
  justify-content: flex-end;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.cart-wishlist-btn {
  font-weight: 800;
  border: none;
  padding: 10px 30px;
  border-radius: 10px;
  font-size: 1.05em;
  color: white;
  background-color: var(--primary-color);
  margin-bottom: 9px;
}
.cart-wishlist-btn:hover {
  background-color: black;
  cursor: pointer;
}

.remove-from-wishlist-btn {
  font-weight: 800;
  border: none;
  padding: 10px 30px;
  border-radius: 10px;
  font-size: 1.05em;
  color: rgb(0, 0, 0);
  background-color: var(--secondary-background-color);
}
.remove-from-wishlist-btn:hover {
  color: black;
  background-color: var(--secondary-background-color-onhover);
  cursor: pointer;
}

.wishlist-empty-container {
  text-align: center;
}

.wishlist-empty-container button {
  text-align: center;
  font-weight: 800;
  border: none;
  border-radius: 20px;
  font-size: 1rem;
  color: white;
  background-color: var(--primary-color);
  padding: 0.5rem 2rem;
}

.wishlist-empty-container button:hover {
  background-color: black;
}
