.products-page-container .product-details-description {
  width: 70vw;
  height: 90%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-evenly;
  line-height: 25px;
}

.product-details-description {
  max-width: 600px; /* 최대 너비 설정 */
  margin: auto; /* 상단, 하단 자동 마진으로 중앙 정렬 */
  display: flex;
  flex-direction: column; /* 세로 방향 정렬 */
  align-items: center; /* 내용을 중앙 정렬 */
  gap: 20px; /* 요소 사이 간격 */
}

.image-preview-container{
  display: flex;
  justify-content: center; /* 수평 중앙 정렬 */
  align-items: center; /* 수직 중앙 정렬 */
  width: 100%; /* 컨테이너의 너비를 설정 */
  margin-top: 10px; /* 위쪽 여백 추가 */

}

.image-preview-container img {
  width: auto; /* 이미지의 원래 비율을 유지하면서 너비 조정 */
  max-width: 100%; /* 컨테이너 너비를 초과하지 않도록 설정 */
  height: auto; /* 이미지의 원래 비율에 맞춰 높이 조정 */
  max-height: 400px; /* 또는 적절한 최대 높이 설정 */
  object-fit: contain; /* 이미지 비율을 유지하면서 최대 너비와 높이에 맞춤 */
  border: 1px dashed #ccc; /* 선택적: 이미지 주변에 테두리 추가 */
  margin-top: 10px; /* 미리보기 이미지 상단 여백 */
  padding: 5px;
}
.image-preview {
  width: calc(20% - 10px); 
  height: auto;
  max-height: 100px; 
  object-fit: cover; 
  border: 1px solid #ccc;
}

.image-upload-button {
  padding: 10px;
  background-color:#424242;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  height: 3rem;
}

.image-upload-button:hover {
  background-color: rgb(66, 66, 66);
}

.product-details-description {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  max-width: 600px;
  margin: 0 auto;
}

.product-details-description form {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 15px;
}

.form-group {
  position: relative;
  padding-top: 15px;
  margin-bottom: 20px;
}

.form-label {
  position: absolute;
  top: -20px; /* 라벨의 위치를 입력 필드 위로 조정 */
  left: 0;
  padding: 0 10px;
  margin: 10px 0;
  transition: all 0.3s ease;
  background-color: white;
  pointer-events: none;
  font-size: 14px; /* 라벨의 초기 크기를 조금 더 작게 조정 */
  color: #999;
}
/* ProductDescription.css 파일에 추가 */

.image-slider-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%; /* 컨테이너의 너비 설정 */
}

.selected-image-preview {
  width: 100%;
  max-width: 600px;
  height: auto;
  object-fit: cover;
  border: 1px dashed #ccc; /* 점선 테두리 스타일 */
  padding: 10px;
  box-sizing: border-box;
}

.thumbnail-images {
  margin-top: 10px;
  display: flex;
  overflow-x: auto; /* 내용이 넘칠 경우 가로로 스크롤 */
  white-space: nowrap; /* 내부 요소들을 한 줄에 표시 */
  gap: 10px; /* 이미지 사이의 간격 */
}

.thumbnail-image {
  display: flex;
  flex-wrap: nowrap; /* 이미지를 한 줄로 나열 */
  gap: 10px; /* 썸네일 사이의 간격 */
  white-space: nowrap;
  overflow-x: auto; /* 가로 스크롤 활성화 */
  max-height: 120px; /* 썸네일 컨테이너의 최대 높이 설정 */
  border: 1px solid #ccc; /* 선택적으로 테두리 추가 */
  padding: 5px; /* 내부 여백 추가 */
  flex: 0 0 auto; /* 이미지 크기가 자동으로 줄어들지 않도록 설정 */
  max-width: 100px; /* 썸네일 이미지의 최대 너비 설정 */
  height: auto; /* 비율에 따라 자동으로 높이 조절 */
  cursor: pointer;
}

.thumbnail-images.scrollable {
  display: flex;
  gap: 10px;
  overflow-x: auto;
  max-height: 120px; /* 또는 적절한 높이 설정 */
}

.thumbnail-image img {
  width: 100%;
  height: auto;
  object-fit: cover; /* 이미지 비율을 유지하면서 채움 */
}

.thumbnail-image.active {
  border-color: #007bff; /* 활성 이미지 스타일 */
}


.form-input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.form-input:focus + .form-label,
.form-input:not(:placeholder-shown) + .form-label {
  /* top: -11px; */
  left: 0;
  color: #007bff;
  font-size: 14px;
}

.product-details-description form label {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.product-details-description form input,
.product-details-description form button,
.product-details-description form input[type="file"] {
  width: 100%;
  padding: 12px 15px; /* 패딩 조정으로 높이 조정 */
  margin-top: 5px;
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  border-radius: 12px;
  box-sizing: border-box;
  outline: none;
  font-size: 16px; /* 글자 크기 조정 */
}

.product-details-description form input:focus,
.product-details-description form select:focus,
.product-details-description form textarea:focus {
  border-color: #4A90E2;
  box-shadow: 0 0 5px rgba(74, 144, 226, 0.5);
}

.product-details-description form button {
  background-color: #424242;
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.product-details-description form button:hover {
  background-color: #000;
}

/* 파일 입력 필드 스타일링 */
.product-details-description form input[type="file"] {
  border: 1px solid #ccc;
  display: inline-block;
  padding: 6px 12px;
  cursor: pointer;
}

.products-page-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px; /* 요소 사이의 간격 */
}

.product-details-image {
  max-width: 600px; /* 이미지 최대 너비 */
  width: 100%; /* 반응형을 위해 너비 100% */
}

.product-details-image img {
  width: 100%; /* 이미지 너비를 컨테이너에 맞춤 */
  max-width: 600px; /* 이미지 최대 너비 제한 */
  height: auto; /* 이미지 높이를 자동으로 조절하여 비율 유지 */
}

.products-page-container .product-details-description h1 {
  margin-bottom: 2rem;
}
.gender-container {
  font-size: 1rem;
}

.description-container {
  font-size: 1.3rem;
  line-height: 22px;
  margin-bottom: 15px;
}

.description-container > span {
  font-weight: 600;
}

.gender-container > span {
  font-weight: 600;
  color: black;
}
.gender-container {
  color: grey;
}

.size-container {
  font-size: 0.9rem;
  color: grey;
}
.size-container > span {
  font-weight: bold;
  color: black;
}

.product-price-container {
  margin-bottom: 20px;
}

.product-original-price {
  font-size: 1.5rem;
  color: grey;
  text-decoration: line-through;
  text-decoration-color: red;
  font-weight: bold;
  margin-right: 10px;
}

.product-discount-price {
  font-size: 1.7rem;
  font-weight: bold;
}
.ratings-reviews {
  font-size: 0.9rem;
  margin-top: -20px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
}

.ratings-reviews > * {
  margin-right: 5px;
}

.ratings-reviews > :first-child {
  font-weight: bold;
}
.ratings-reviews .review {
  color: grey;
  font-size: 1.2rem;
}

.tags {
  display: flex;
  margin-bottom: 15px;
}
.tags > * {
  margin-right: 10px;
}

.product-card-buttons-container {
  display: flex;
  flex-flow: column nowrap;
  margin-bottom: 30px;
}

.product-card-buttons-container .add-to-cart-btn {
  font-weight: 800;
  border: none;
  margin-bottom: 8px;
  border-radius: 10px;
  font-size: 1em;
  color: white;
  background-color: var(--primary-color);
  padding: 15px 45px;
  text-transform: uppercase;
}

.product-card-buttons-container .add-to-cart-btn:hover {
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
  cursor: pointer;
}

.product-card-buttons-container .add-to-wishlist-btn {
  border: none;
  background-color: rgb(232, 232, 232);
  color: var(--primary-color);
  border-radius: 10px;
  padding: 15px 45px;
  font-weight: 800;
  text-transform: uppercase;
  font-size: 1em;
}

.product-card-buttons-container .add-to-wishlist-btn:hover{
  background-color: rgb(214, 214, 214);
  color: rgb(0, 0, 0);
  box-shadow: none;
}

@media (min-width: 768px) {
  .products-page-container .product-details-description {
    width: 25vw;
    height: 300px;
    justify-content: space-between;
    line-height: 26px;
    margin-top: -30px;
  }

  .tags {
    display: flex;
    margin-bottom: 30px;
  }
}
