    /* ------sort----------- */
  
    .filter-types-container .sorting-container {
      display: flex;
      flex-flow: column nowrap;
    }
  
    .sorting-input-container {
      display: flex;
      flex-flow: column;
    }
  
    .sorting-input-container > * {
      /* border: 1px solid yellow; */
      margin-bottom: 0.6rem;
    }
  
    .sorting-input-container > label {
