.order-info {
  background-color: #fff;
  /* 배경색 */

  padding: 20px;
  /* 내부 여백 */
  margin: 0px 0;
  /* 상하 마진 */

}

.order-info h2 {
  color: #495057;
  /* 제목 색상 */
  margin-bottom: 15px;
  /* 제목 아래 여백 */
  padding-bottom: 10px;
  /* 제목 아래 패딩 */
}

/*  */
.order-info-section {
  margin-bottom: 20px;
}

.order-info-section >h3 {
  margin-bottom: 10px;
}

.order-info-section>* {
  display: flex;
  justify-content: space-between;
  padding: 5px 10px;
  color: grey;
}

.orer-img-container {
  color: gray;
  font-size: 20px;
  font-weight: 600;
}

.orer-img-container>p {
  margin-bottom: 10px;
}


.order-info-name {
  font-size: 15px;
  /* font-weight: 600; */
  color: grey;
}

.order-img-order> p{
  margin-bottom: 20px;
  text-align: center;
}

.order-img-address> p{
  margin-bottom: 20px;
  text-align: center;
}

.order-img-order {
  border-top: 1px solid rgb(226, 226, 226);
  padding: 20px 0px;

}

.order-img-address{
  border-top: 1px solid rgb(226, 226, 226);
  padding-top: 20px;
}

.order-info-phon {
  font-size: 15px;
  color: grey;
}

.order-info p:last-child {
  margin-bottom: 0;
  /* 마지막 문단 하단 마진 제거 */
}

/* 이미지 리스트 스타일 */
.order-info .image-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  /* 이미지 사이 간격 */
}

.order-info img {
  width: 100%;
  /* 이미지의 너비를 컨테이너의 너비에 맞춥니다. */
  height: auto;
  /* 이미지의 높이를 자동으로 조정하여 비율을 유지합니다. */
  object-fit: cover;
  /* 이미지의 비율을 유지하면서 컨테이너를 꽉 채우도록 조정합니다. */
  border-radius: 8px;
  /* 필요한 경우 이미지에 둥근 모서리를 추가합니다. */
}

.order-info .image-list img {
  width: 100px;
  /* 이미지 너비 */
  height: 100px;
  /* 이미지 높이 */
  object-fit: cover;
  /* 이미지 비율 유지 */
  border-radius: 5px;
  /* 이미지 둥근 모서리 */
}

@media (min-width: 768px) {
  .order-info {
    flex: 1;
    /* flex-grow, flex-shrink 및 flex-basis를 설정 */
    margin: 20px;
    /* 주변 마진 */
  }
}