/* ImageUploadModal.css */
.modalContainer {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: black;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    z-index: 1000;
  }
  
  .modalHeader {
    margin-bottom: 20px;
    text-align: center;
  }
  
  .modalButton {
    display: inline-block;
    margin-right: 10px;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background-color: #4CAF50;
    color: white;
  }
  
  .modalButton:last-child {
    margin-right: 0;
  }
  
  .modalCloseButton {
    background-color: #f44336;
  }
  
  .modalUploadButton {
    background-color: #4CAF50;
  }
  
  .modalInput {
    margin-bottom: 20px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: calc(100% - 22px); /* input padding and border */
  }
  
  .imagePreviewContainer {
    margin-bottom: 5px;
    text-align: center;
  }
  
  .imagePreview {
    max-width: 100%; /* 컨테이너 너비를 초과하지 않도록 설정 */
    max-height: 200px; /* 미리보기 이미지의 최대 높이 */
    object-fit: cover; /* 이미지 비율 유지 */
  }